import {rechargeOnline,goPay} from '@/Api/NetWork'
	export default{
		data(){
			return{
				list:[],
				currentIndex:0,
				token:''
			}
		},
		onLoad() {
			this.token = this.$store.state.userInfo.token
			rechargeOnline().then(res => {
				// this.list = res
				const list = res
				list.forEach((item,index) => {
					if(index == 0){
						item.type = true
					}else{
						item.type == false
					}
				})	
				this.list = list
				console.log(list)
			})
		},
		methods:{
			//立即充值
			recharge(){
				// var thta = this 
				// uni.showLoading({
				// 	title:'加载中...',
				// })
				// setTimeout(function(){
				// 	uni.hideLoading()
				// 	uni.reLaunch({
				// 		url:'../succeed/succeed?id=0'
				// 	})
				// },1500)
				if(this.token){
					// 发起微信支付
					const id = this.list[this.currentIndex].id
					goPay(this.token,id)
						.catch(err => {
							uni.showToast({
								title:'支付失败',
								icon:'none'
							})
						})
						.then(res => {
							// 拿到请求对象
							let data = res
							// console.log(typeof(data.timeStamp))
							data.timeStamp = data.timeStamp.toString()
							console.log(data)
							// 发起微信支付
							// uni.requestPayment({
							// 	timeStamp:data.timestamp.toString(),
							// 	nonceStr:data.nonceStr,
							// 	package:'prepay_id=' + data.package,
							// 	signType:data.signType,
							// 	paySign:data.paySign,
							// 	provider:'wxpay',
							// 	orderInfo:data,
							// 	appId:data.appId,
							// 	// _debug:1,
							// 	// service:1,
							// 	success:(res) => {
							// 		// 接口调用成功
							// 		console.log(res)
							// 	},
							// 	fail:(err) => {
							// 		console.log(err)
							// 		// 接口调用失败
							// 	}
								
							// })

							// 发起微信支付
							uni.requestPayment({
								provider:'wxpay',
								orderInfo:data,
								nonceStr:data.nonceStr,
								package:data.package,
								paySign:data.paySign,
								signType:data.signType,
								timeStamp:data.timeStamp,
								_debug:1,
								service:1,
								success:(res) => {
									// 接口调用成功
									console.log(res)
									if(res.errMsg == 'requestPayment:ok'){
										uni.redirectTo({
											url:'../succeed/succeed?id=0'
										})
									}
								},
								fail:(err) => {
									// 接口调用失败	
									console.log(err)
								}
							})

							
							
						})
						
				}else{
					uni.showToast({
						title:'登陆未授权',
						icon:'none'
					})
				}
			},
			//选择金额
			rechargeClick(type,key){
				this.currentIndex = key
			}
		}
	}
